/*** Range transparencia ***/

.m-toc-activelayers .transparency-control {
    display: flex;
    align-items: center;
}

.m-toc-activelayers .transparency-percentage {
    margin-left: 5px;
}

.m-toc-activelayers input[type=range] {
    -webkit-appearance: none;
    width: 100%;
    margin: 9.75px 0;
    height: 3ex;
}

.m-toc-activelayers input:disabled {
    cursor: not-allowed
}

.m-toc-activelayers input[type=range]:focus {
    outline: none;
}

.m-toc-activelayers input[type=range]::-webkit-slider-runnable-track {
    width: 100%;
    height: 1.5px;
    cursor: pointer;
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
    background: #404040;
    border-radius: 7.7px;
    border: 0px solid #010101;
}

.m-toc-activelayers input[type=range]::-webkit-slider-thumb {
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
    border: 1px solid #000000;
    height: 21px;
    width: 9px;
    border-radius: 3px;
    background: #dcdcdc;
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -9.75px;
}

.m-toc-activelayers input[type=range]:focus::-webkit-slider-runnable-track {
    background: #404040;
}

.m-toc-activelayers input[type=range]::-moz-range-track {
    width: 100%;
    height: 1.5px;
    cursor: pointer;
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
    background: #404040;
    border-radius: 7.7px;
    border: 0px solid #010101;
}

.m-toc-activelayers input[type=range]::-moz-range-thumb {
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
    border: 1px solid #000000;
    height: 21px;
    width: 9px;
    border-radius: 3px;
    background: #dcdcdc;
    cursor: pointer;
}

.m-toc-activelayers input[type=range]::-ms-track {
    width: 100%;
    height: 1.5px;
    cursor: pointer;
    background: transparent;
    border-color: transparent;
    color: transparent;
}

.m-toc-activelayers input[type=range]::-ms-fill-lower {
    background: #404040;
    border: 0px solid #010101;
    border-radius: 15.4px;
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
}

.m-toc-activelayers input[type=range]::-ms-fill-upper {
    background: #404040;
    border: 0px solid #010101;
    border-radius: 15.4px;
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
}

.m-toc-activelayers input[type=range]::-ms-thumb {
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
    border: 1px solid #000000;
    height: 21px;
    width: 9px;
    border-radius: 3px;
    background: #dcdcdc;
    cursor: pointer;
    height: 1.5px;
}

.m-toc-activelayers input[type=range]:focus::-ms-fill-lower {
    background: #404040;
}

.m-toc-activelayers input[type=range]:focus::-ms-fill-upper {
    background: #404040;
}

