        z-index: 999999;
        box-shadow: none;
    }
}

.m-areas>div.m-area>div.m-panel.m-managelayers.collapsed>button.m-panel-btn {
    color: #00897B;
    position: inherit;
    right: 0;
    box-shadow: none;
}

/* ANIMATIONS */

.m-areas>div.m-area>div.m-panel.m-managelayers {
    -webkit-transition: max-height .33s ease-in-out 0s;
    transition: max-height .33s ease-in-out 0s;
    max-height: 100vh;
}

.m-areas>div.m-area>div.m-panel.m-managelayers.collapsed {
    -webkit-transition: max-height .33s ease-in-out 0s;
    transition: max-height .33s ease-in-out 0s;
    max-height: 40px;
}

.m-areas>div.m-area>div.m-panel.m-managelayers>div.m-panel-controls {
    -webkit-transition: max-width .5s ease 0s, opacity .44s ease 0s;
    transition: max-width .5s ease 0s, opacity .44s ease 0s;
    background: white;
    width: 100%;
}

.m-managelayers-container>.results-panel>div.results {
    -webkit-transition: max-height .33s ease-in-out 0s;
    transition: max-height .33s ease-in-out 0s;
}

.m-areas>div.m-area>div.m-panel.m-managelayers.opened {
    width: 440px;
}

@media only screen and (max-width: 768px) {
    .m-areas>div.m-area>div.m-panel.m-managelayers.opened {
        margin-left: 0;
        width: 100vw;
        height: 100vh;
        position: fixed;
        top: 0;
        left: 0;
        margin: 0;
        z-index: 10000;
        overflow: auto;
    }
    #toolbar-containers-managelayers {
        max-height: calc(100vh - 81.5px);
    }
}


